@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.single-application {
  cursor: pointer;
  display: flex;
  align-items: center;
  margin-right: auto;
  margin-bottom: 3rem;
  width: 10rem;
  .single-application-dummy {
    background-color: $appsFillGray;
    border: 1px solid #000;
    height: 2.5rem;
    min-width: 2.5rem;
  }
  label {
    color: $black;
    font-size: 0.7rem;
    margin-left: 0.7rem;
  }
}
