@import "./variables.scss";

html {
  box-sizing: border-box;
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  body,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  ol,
  ul {
    margin: 0;
    padding: 0;
    font-weight: normal;
    ol,
    ul {
      list-style: none;
    }

    img {
      max-width: 100%;
      height: auto;
    }
  }
  form,
  fieldset,
  input,
  button {
    border: none;
    outline: none;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}
